
// ./AddressItem.tsx
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Edit2 } from "lucide-react";
import { useState } from "react";

// Reusable AddressItem (sub-component for AddressesSection)
// Props: address, onSave
// Handles its own editing state
export default function AddressItem({ address, onSave }) {
  const [isEditing, setIsEditing] = useState(false);
  const [editedAddress, setEditedAddress] = useState(address);

  const handleSave = () => {
    onSave(editedAddress);
    setIsEditing(false);
  };

  return (
    <Card className="border-2">
      <CardContent className="p-4">
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <div className="flex items-center gap-2 mb-2">
              {/* Label, type badge, default badge as in original */}
            </div>
            {isEditing ? (
              <div className="space-y-3">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                  <Input
                    placeholder="Address Line 1"
                    value={editedAddress.line1}
                    onChange={(e) => setEditedAddress({ ...editedAddress, line1: e.target.value })}
                  />
                  {/* ... other inputs */}
                </div>
                <div className="flex gap-2">
                  <Button size="sm" onClick={handleSave}>
                    Save
                  </Button>
                  <Button size="sm" variant="outline" onClick={() => setIsEditing(false)}>
                    Cancel
                  </Button>
                </div>
              </div>
            ) : (
              <div className="text-sm text-muted-foreground">
                {/* Display address as in original */}
              </div>
            )}
          </div>
          {!isEditing && (
            <Button variant="outline" size="sm" onClick={() => setIsEditing(true)}>
              <Edit2 size={14} />
            </Button>
          )}
        </div>
      </CardContent>
    </Card>
  );
}