import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import AddressItem from "./AddressItem"; // New sub-component

// Reusable AddressesSection
// Props: addresses, setAddresses
export default function AddressesSection({ addresses, setAddresses }) {
  return (
    <Card>
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle>My Addresses</CardTitle>
          <Button>Add New Address</Button>
        </div>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {addresses.map((address) => (
            <AddressItem
              key={address.id}
              address={address}
              onSave={(updatedAddress) => {
                // Update logic
                setAddresses(addresses.map(a => a.id === updatedAddress.id ? updatedAddress : a));
              }}
            />
          ))}
        </div>
      </CardContent>
    </Card>
  );
}
