"use client";

import { GET_USER_PROFILE_DETAILS } from "@/client/user/user.queries";
import AddressesSection from "@/components/page/account/AddressesSection";
import ProfileSection from "@/components/page/account/ProfileSection";
import { useQuery } from "@apollo/client";
import { useState } from "react";
interface UserProfile {
  id: string;
  email: string;
  firstName: string | null;
  lastName: string | null;
  phone: string | null;
}

interface Address {
  id: string;
  type: "SHIPPING" | "BILLING" | "BUSINESS" | "WAREHOUSE";
  label: string | null;
  line1: string;
  line2: string | null;
  city: string;
  state: string;
  country: string;
  postalCode: string;
  phone: string | null;
  isDefault: boolean;
}

interface Order {
  id: string;
  orderNumber: string;
  status:
    | "PENDING"
    | "CONFIRMED"
    | "PROCESSING"
    | "SHIPPED"
    | "DELIVERED"
    | "CANCELLED"
    | "RETURNED";
  total: number;
  createdAt: string;
}

const mockUser: UserProfile = {
  id: "1",
  email: "john.doe@example.com",
  firstName: "John",
  lastName: "Doe",
  phone: "+1234567890",
};

const mockAddresses: Address[] = [
  // ... (as in original)
];

const mockOrders: Order[] = [
  // ... (as in original)
];

export default function AccountPage() {
  const [activeTab, setActiveTab] = useState("profile");
  const [user, setUser] = useState(mockUser);
  const [addresses, setAddresses] = useState(mockAddresses);
  const [orders] = useState(mockOrders);

  const { data: userProfileDetails, loading, error } = useQuery(GET_USER_PROFILE_DETAILS);
  if (loading) console.log("Loading user profile");
  if (error) console.log("User profile error", error);
  console.log("userProfileDetails-->", userProfileDetails);

  // Render the active section based on tab
  const renderActiveSection = () => {
    switch (activeTab) {
      case "profile":
        return <ProfileSection user={user} setUser={setUser} />;
      case "addresses":
        return <AddressesSection addresses={addresses} setAddresses={setAddresses} />;
      case "orders":
        return <OrdersSection orders={orders} />;
      case "wishlist":
        return <WishlistSection />;
      case "payment":
        return <PaymentMethodsSection />;
      case "notifications":
        return <NotificationsSection />;
      case "security":
        return <SecuritySection />;
      case "settings":
        return <SettingsSection />;
      default:
        return null;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      <div className="container mx-auto py-8">
        <div className="flex flex-col lg:flex-row gap-8">
          <SidebarNav
            user={user}
            activeTab={activeTab}
            setActiveTab={setActiveTab}
          />
          <div className="flex-1">{renderActiveSection()}</div>
        </div>
      </div>
    </div>
  );
} 